@import url(../../node_modules/normalize.css/normalize.css);
* {
  box-sizing: border-box; }

body {
  font-family: "Helvetica Neue",Helvetica,"Microsoft Yahei",Arial,sans-serif;
  font-size: 16px;
  color: #333; }

input {
  outline: 0; }

.textbox {
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 7px 10px;
  display: inline-block;
  width: 100%;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075); }
  .textbox:focus {
    outline: 0; }

.btn {
  display: inline-block;
  outline: 0;
  color: #fff;
  border-style: none;
  border-radius: 3px;
  padding: 5px 16px;
  text-align: center; }
  .btn:active {
    box-shadow: inset 0 5px 5px rgba(0, 0, 0, 0.125); }
  .btn.primary {
    background-color: #2483D2; }
    .btn.primary:disabled, .btn.primary.disable {
      background-color: #8CCBFF;
      cursor: auto; }
  .btn.default {
    background-color: #fff;
    color: inherit;
    border: 1px solid #ccc; }
    .btn.default:active, .btn.default:hover {
      background-color: #eee; }

.clearfix:before, .clearfix:after {
  display: table;
  content: ''; }

.clearfix:after {
  clear: both; }

.hidden {
  display: none; }

.help-text {
  font-size: .8em; }

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }
  @media screen and (min-width: 768px) {
    .container {
      width: 750px; } }
  @media screen and (min-width: 992px) {
    .container {
      width: 970px; } }
  @media screen and (min-width: 1200px) {
    .container {
      width: 1170px; } }

.pull-right {
  float: right !important; }

.markdown h1, .markdown h2 {
  padding-bottom: 8px;
  border-bottom: 1px dotted #ccc; }

.markdown a {
  color: #0081FF;
  text-decoration: none; }
  .markdown a:hover {
    color: #339AFF; }

.markdown pre {
  background-color: #eee;
  color: #555;
  border-radius: 3px;
  padding: 10px;
  max-height: 500px;
  overflow: auto;
  border: none;
  font-size: 0.9em;
  line-height: 1.33; }
  .markdown pre code {
    font-size: inherit;
    color: inherit;
    padding: 0;
    background-color: transparent; }

.markdown code {
  color: #c7254e;
  background-color: #eee;
  font-size: 0.9em;
  padding: 2px 4px; }

.markdown blockquote {
  margin: 0;
  padding: 5px 10px 5px 15px;
  border-left: 5px solid steelblue;
  background-color: #eee; }

.markdown table {
  border: 1px solid #ccc; }
  .markdown table thead tr {
    background-color: #eee; }
  .markdown table tbody tr {
    border-top: 1px solid #ccc;
    background-color: #fff; }
  .markdown table th, .markdown table td {
    padding: 8px;
    border-left: 1px solid #ccc; }

.markdown hr {
  border-style: solid;
  border-color: #ccc; }

#editor {
  padding-bottom: 20px;
  width: 100%;
  max-width: 1600px; }

.editor-title {
  font-size: 18px; }

.editor-title .textbox,
.md-editor textarea,
.md-preview,
.tag-input input {
  line-height: 1.5; }

/* Style for Tag Input */
.tag {
  display: inline-block;
  padding: 3px 8px;
  margin-right: 5px;
  color: steelblue;
  background-color: lightblue;
  border-radius: 3px;
  font-size: .8em;
  font-weight: 300; }

.tag-input {
  position: relative;
  margin: 20px 0;
  cursor: text;
  background-color: #fff; }
  .tag-input input {
    border: none;
    padding: 2px 0;
    margin-left: 3px;
    font-size: .8em; }
    .tag-input input:focus {
      outline: 0; }
  .tag-input .hidden {
    position: absolute;
    opacity: 0;
    left: 0; }

/* Style for Editor */
.md-panel {
  display: block;
  position: relative;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 14px;
  overflow: hidden; }
  .md-panel.fullscreen {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }
    .md-panel.fullscreen .md-editor {
      height: 100%; }

.md-menubar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 37px;
  border-bottom: 1px solid #ccc;
  box-shadow: 0 0 1px #ccc;
  z-index: 10; }

.md-toolbar, .md-modebar {
  margin: 0; }

.md-toolbar {
  padding: 0 5px;
  background-color: #fff; }

.tb-btn {
  float: left;
  font-size: 16px;
  color: #aaa;
  list-style: none;
  margin: 0 2px; }
  .tb-btn.spliter {
    border-left: 1px solid #ccc;
    margin: 6px 2px;
    height: 24px; }
  .tb-btn a {
    display: inline-block;
    width: 36px;
    height: 36px;
    text-align: center;
    padding: 9px 0;
    cursor: pointer; }
    .tb-btn a:hover, .tb-btn a.active {
      color: #0081FF;
      background-color: #eee; }
    .tb-btn a:active {
      box-shadow: inset 0 1px 5px rgba(0, 0, 0, 0.075); }

.md-editor {
  width: 50%;
  height: auto;
  transition: width 0.3s;
  background-color: #fff; }
  .md-editor.expand {
    width: 100%; }
  .md-editor textarea {
    border-style: none;
    resize: none;
    outline: 0;
    height: 100%;
    min-height: 540px;
    width: 100%;
    padding: 47px 15px 0 15px; }

.md-preview {
  position: absolute;
  width: 50%;
  height: 100%;
  left: 50%;
  top: 0;
  background-color: #f6f6f6;
  padding: 45px 20px 20px 20px;
  border-left: 1px solid #ccc;
  overflow: auto;
  transition: left 0.3s, width 0.3s; }
  .md-preview.expand {
    left: 0;
    width: 100%;
    border-left-style: none; }
  .md-preview.shrink {
    left: 100%;
    width: 0; }

.md-spliter {
  position: absolute;
  top: 0;
  left: 50%;
  height: 37px;
  border-left: 1px solid #ccc;
  z-index: 105; }

.md-resizer {
  position: absolute;
  width: 50px;
  height: 4px;
  left: 50%;
  margin-left: -25px;
  bottom: -7px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  cursor: row-resize; }

.md-btngroup {
  margin-top: 20px; }
  .md-btngroup .help-text {
    margin-left: 15px; }
